
import $ from 'jquery';
export default {
    name: 'Navbar-Component',
    props: ['activeMenu'],
    data() {
        return {
            isOpen: false
        }
    },
    methods: {
        logout: function() {
            this.$store.dispatch('logOut')
			this.$router.push({ name: 'login' })
        },
    },

    async mounted(){
        this.$nextTick(()=>{
            $(window).scroll(function() {
              if ($(this).scrollTop() > 100) {
                $('#header').addClass('header-scrolled');
                $('.tt-logo').attr("src","https://i.imgur.com/eLwiSXU.png");
              } else {
                $('#header').removeClass('header-scrolled');
                $('.tt-logo').attr("src","https://i.imgur.com/eLwiSXU.png");
              }
        
              
            });
            window.onscroll = function() {
                $("#header").addClass('show-head');
            } 
        
            $(window).scroll(function() {
              if ($(this).scrollTop() > 100) {
                $('#shop-header').addClass('header-scrolled');
              } else {
                $('#shop-header').removeClass('header-scrolled');
              }
            });
        
            $(window).scroll(function() {
              if ($(this).scrollTop() > 0) {
                $('.colhead').addClass('header-scrolled');
              } else {
                $('.colhead').removeClass('header-scrolled');
              }
            });
          
            if ($(window).scrollTop() > 100) {
              $('#header').addClass('header-scrolled');
            }
        
            if ($(window).scrollTop() > 100) {
              $('#shop-header').addClass('header-scrolled');
            }
        })
    }
}